// ============================================================================
// Media | SVG
// ============================================================================

svg {
    @include hyperlink_base;
}

svg * {
    @include hyperlink_base;
    pointer-events: none;
}

.svg_container {
    display: inline-block;
    position: relative;
    width: 100%;
    padding-bottom: 100%;
    vertical-align: top;
    overflow: hidden;
}

.svg_container svg {
    display: inline-block;
    position: relative;
    width: 100%;
    // position: absolute;
    // top: 0;
    // left: 0;
}
