// ============================================================================
// Typography | Character | Italic
// ============================================================================

@mixin character_italic {
    @include character_normal;
    // @include font_weight_03;
    // @include font_size_00;
    @include character_default;
    @include font_weight_300;
    font-style: italic;
}

i {
    @include character_italic;
}
